<template>
    <div class="largeContain">
        <div class="title">Doomsday</div>
        <img src="./assets/refresh.svg" class="retry" @click="retry"/>
        <menubar></menubar>
        <div class="inputContain"> 
            <div class="timeText" v-show="!start" @click="dialogVisible=true">{{hourIpt}}:{{minIpt}}:{{secIpt}}</div>
            <el-dialog title="Edit Timer" :visible.sync="dialogVisible" :before-close="handleClose">
                <div class="dialogContain">
                    <div class="dialogInputContain">
                        <span>Hrs</span>
                        <el-input v-model="hourIpt"></el-input>
                    </div>
                    <div class="dialogInputContain">
                        <span>Mins</span>
                        <el-input v-model="minIpt"></el-input>
                    </div>
                    <div class="dialogInputContain">
                        <span>Secs</span>
                        <el-input v-model="secIpt"></el-input>
                    </div>
                </div>
                <span slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="dialogVisible = false; time = generateTime()">Done</el-button>
                </span>
            </el-dialog>
            <countdown ref="countdown" v-if="start" :time="time" class="countdown" :abort="pause"
            @end="countdownEnd" v-slot="{hours, minutes, seconds}" :transform="transformSlotProps">
                {{hours}}:{{minutes}}:{{seconds}}
            </countdown>
        </div>
        <div class="selectContain">
            <div style="width: 80%;display:flex;flex-dire1ction:row;justify-content:space-between">
                <div style="text-align: left; align-self: center; width: 50%;">Year interval:</div>
                <div class="intervalContain">
                    <span style="align-self: center">From</span>
                    <el-input v-model="st" style="width:30%;margin-left:0.5em" @focus="onInput()" @blur="leaveInput()" v-if="start==false"></el-input>
                    <el-input v-model="st" style="width:30%;margin-left:0.5em" disabled v-else></el-input>
                    <span style="align-self: center;margin-left:0.5em">to</span>
                    <el-input v-model="ed" style="width: 30%;margin-left:0.5em" @focus="onInput()" @blur="leaveInput()" v-if="start==false"></el-input>
                    <el-input v-model="ed" style="width: 30%;margin-left:0.5em" disabled v-else></el-input>
                </div>
            </div>
        </div>
        <div class="dateContain">
            <div style="width: 80%;display:flex;flex-direction:row;justify-content:space-between">
                <el-button class="startButton" @click="startGame" v-if="(!start) && (time != 0)">Start</el-button>
                <el-button class="startButton" disabled v-else>Start</el-button>
                <div style="text-align: center; align-self: center">{{randomDate}}</div>
            </div> 
        </div>
        <div class="optContain">
            <div style="width: 80%;display:flex;flex-direction:row;justify-content:space-between; flex-wrap: wrap">
                <div v-for="i,idx in opt" :key="idx" style="margin-top: 1em"> 
                    <el-button @click="selectedDate(idx)" v-if="start">{{i}}</el-button>
                    <el-button @click="selectedDate(idx)" disabled v-else>{{i}}</el-button>
                </div>
            </div>
        </div>
        <div> 
        </div>
    </div>
</template>

<script>
    import menubar from './menu.vue'
    export default{
        name: 'date',
        data(){
            return {
                randomDate: 'Random date will be displayed here',
                opt: ['Sun','Mon','Tue','Wed','Thu','Fri','Sat'],
                st: '',
                ed: '',
                ipt: [0,0,0,0,0,0],
                start: false,
                ans: '',
                response: [],
                time: 0,
                pause: false,
                enterInput: false,
                hourIpt: '0',
                minIpt: '0',
                secIpt: '0',
                dialogVisible: false,
            }
        },
        components: {
            menubar
        },
        methods: {
            retry(){
                this.$refs.countdown.abort()
                this.$confirm('Do you want to restart the round?', 'Retry', {
                    confirmButtonText: 'Yes',
                    cancelButtonText: 'No',
                    type: 'warning'
                }).then(()=>{
                    this.hourIpt = '0'
                    this.minIpt = '0'
                    this.secIpt = '0'
                    this.start = false
                    this.time = 0
                    this.randomDate = 'Random date will be displayed here'
                    this.response = []
                    this.$refs.countdown.end()
                }).catch(()=>{
                    this.$refs.countdown.start()
                })
            },
            stringToNum(str){
                var res = 0
                for (let i = 0; i < str.length; i++){
                    res = res * 10 + (str[i].charCodeAt(0)-'0'.charCodeAt(0))
                }
                return res
            },
            generateTime(){
                //val.charCodeAt(0)-'0'.charCodeAt(0)
                var hour = this.stringToNum(this.hourIpt)
                var min = this.stringToNum(this.minIpt)
                var sec = this.stringToNum(this.secIpt)
                this.time = (hour*3600+min*60+sec)*1000
            },
            generateDate(){
                var st = new Date(parseInt(this.st),1,1)
                var ed = new Date(parseInt(this.ed),12,1)
                const mon = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']
                var raw = new Date(st.getTime()+Math.random()*(ed.getTime()-st.getTime()))
                this.randomDate = mon[raw.getMonth()] + ", " + raw.getDate() + ", " + raw.getFullYear()
                this.ans = raw.getDay()
            },
            startGame(){
                this.generateTime()
                this.generateDate()
                this.start = !this.start
                this.startCount = !this.startCount
            },
            selectedDate(val){
                console.log(this.ans)
                this.response.push(val==this.ans)
                if(this.ans == val){
                    this.$message({
                        message: "Correct!",
                        type: "success",
                        duration: 1500
                    }) 
                } else {
                    this.$message({
                        message: "Incorrect!",
                        type: "error",
                        duration: 1500
                    })
                }
                this.generateDate()
            },
            countdownEnd(){
                var cnt = 0
                for(let i = 0; i < this.response.length; i++){
                    if(this.response[i]==true){
                        cnt+=1
                    }
                }
                this.$message({
                    message: 'You got ' + String(cnt) + ' out of ' + String(this.response.length) + ' right!', 
                    type: "success",
                    duration: 4000
                })
                this.hourIpt = '0'
                this.minIpt = '0'
                this.secIpt = '0'
                this.time = 0
                this.start = !this.start
                this.randomDate = 'Random date will be displayed here'
                this.response = []
            },
            transformSlotProps(props){
                const formattedProps = {};

                Object.entries(props).forEach(([key, value]) => {
                    formattedProps[key] = value < 10 ? `0${value}` : String(value);
                });

                return formattedProps;
            },
            onInput(){
                this.enterInput = true
            },
            leaveInput(){
                this.enterInput = false
                console.log(this.st, this.ed)
            },
            handleClose(done){
                this.time = this.generateTime()
                done()
            }
        }
    }
</script>

