import Vue from 'vue'
import Router from 'vue-router'
import main from '../components/main'
import wordle from '../components/wordle'
import date from '../components/date'
import mbcalc from '../components/mbcalc'
Vue.use(Router)
export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/kdhub/',
            name: 'main',
            component: main
        },
        {
            path: '/kdhub/wordle',
            name: 'wordle',
            component: wordle
        },
        {
            path: '/kdhub/date',
            name: 'date',
            component: date
        },
        {
            path: '/kdhub/mbcalc',
            name: 'mbcalc',
            component: mbcalc
        }
    ]
})